import React, {useState} from 'react';
import AuthorComponent from '../views/authorView/AuthorComponent';

const ComponentPopup = (props) => {
    const [popupData, setPopupData] = useState(null);
    const {id, children, lang, type,author_id} = props;
    const popupDataFunc = (id) => {popupData?setPopupData(false):setPopupData(id)}
    return (
        <div className="table-row-content">
            <div className="popupArea" onClick={() => popupDataFunc(id)}>{children}</div>
                {popupData&&id&&
                    <div className = "popup">
                        <p><a href={`/author/${type==="author"?id:author_id}${type==="text"?'/text/'+id:''}`}>Go to the Page</a><button onClick = {() =>setPopupData(null)}> X</button></p>
                        {<AuthorComponent className="popup" properties={{id:popupData, lang}}/>}
                    </div>}
        </div>
    )
}

export default ComponentPopup